import { connect } from 'react-redux';

import { getMethod } from '../../store/actions/globalActn';
import Sales from '../components/Sales';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const SalesCnt = connect(mapStateToProps, mapDispatchToProps)(Sales);
export default SalesCnt;
