import { connect } from 'react-redux';

import { getMethod } from '../../../store/actions/globalActn';
import Providers from '../../components/InventorySections/Providers';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const ProvidersCnt = connect(mapStateToProps, mapDispatchToProps)(Providers);
export default ProvidersCnt;
