import { connect } from 'react-redux';

import { goTo, getMethod } from '../../store/actions/globalActn';
import Dashboard from '../components/Dashboard';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  goTo: (path) => dispatch(goTo(path)),
  getMethod: (path) => dispatch(getMethod(path)),
})

const DashboardCnt = connect(mapStateToProps, mapDispatchToProps)(Dashboard);
export default DashboardCnt;
