import { connect } from 'react-redux';

import { getMethod } from '../../store/actions/globalActn';
import Customers from '../components/Customers';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const CustomersCnt = connect(mapStateToProps, mapDispatchToProps)(Customers);
export default CustomersCnt;
