import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import styled from 'styled-components';

class Reports extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
  })

  render() {
    return (
      <Wrapper>
        <p>Here I am a new Reportes</p>
      </Wrapper>
    );
  }
}

