import { EditFilled, PlusCircleOutlined, SaveOutlined } from '@ant-design/icons';
import { Button, Input, Table } from 'antd';
import Modal from 'antd/lib/modal/Modal';
import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import styled from 'styled-components';

import { ModalBody } from '../../../common/misc/ModalBody';
import { NormalText2 } from '../../../common/texts/Normal';
import { TitleText, TitleText2 } from '../../../common/texts/Titles';
import detail from '../../../data/detail';
import { textColumn } from '../../../helpers/columnDefinition';
import { createNotification } from '../../../helpers/notifications';

const columns = [
  {
    title: 'Proveedor',
    dataIndex: 'providername',
    key: 'providername',
    width: '30%',
    ellipsis: true,
    align: 'left',
    render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${value}`}</p>,
  },
  {
    title: 'Contacto',
    dataIndex: 'contact',
    key: 'contact',
    width: '20%',
    ellipsis: true,
    align: 'left',
    render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${value}`}</p>,
  },
  {
    title: 'Telfono',
    dataIndex: 'phone',
    key: 'phone',
    width: '15%',
    ellipsis: true,
    align: 'left',
    render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${value}`}</p>,
  },
  {
    title: 'Email',
    dataIndex: 'email',
    key: 'email',
    width: '25%',
    ellipsis: true,
    align: 'left',
    render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${value}`}</p>,
  },
  {
    title: '',
    dataIndex: 'key',
    width: '10%',
    key: 'key',
    render: value => (
      <div>
        <Button
          style={{ backgroundColor: '#FFF' }}
          type="primary"
          size="small"
          onClick={() => createNotification('info', `En desarrollo ${value}`, 'Funcionalidad en desarrollo')}
          icon={<EditFilled style={{ color: '#000' }} />}
        />
      </div>
    ),
  },
];

class Providers extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
    openModal: false,
    phoneValue: '',
  })

  getNewSaleButtonConfigStyle = () => ({
    fontSize: '12pt',
    fontWeight: 600,
    marginLeft: 20,
    height: 50,
    width: '200px',
    borderRadius: 5,
    backgroundColor: '#2185D0',
  })

  getColumns = () => [
    textColumn('Proveedor', 'providername', '30%', 'left', '', ''),
    textColumn('Contacto', 'contact', '20%', 'left', '', ''),
    textColumn('Telfono', 'phone', '15%', 'left', '', ''),
    textColumn('Email', 'email', '25%', 'left', '', ''),
    {
      title: '',
      dataIndex: 'key',
      width: '10%',
      key: 'key',
      render: value => (
        <div>
          <Button
            style={{ backgroundColor: '#FFF' }}
            type="primary"
            size="small"
            onClick={() => createNotification('info', `En desarrollo ${value}`, 'Funcionalidad en desarrollo')}
            icon={<EditFilled style={{ color: '#000' }} />}
          />
        </div>
      ),
    },
  ]

  onChangeInput = (e) => {
    const { name, value } = e.target;
    console.log(e);
    console.log('Name:', name, 'Value:', value);

    function format(value, pattern) {
      let i = 0;
      const v = value.toString();
      return pattern.replace(/#/g, _ => v[i++] || '');
    }

    console.log(format(value, '####-####'));
    // this.setState({ phoneValue: format(value, '####-####') })
  }

  render() {
    const { openModal, phoneValue } = this.state;
    return (
      <Wrapper>
        <div className="header">
          <TitleText content={'Proveedores'} />
          <div className="dec-line" />
          <Button
            type={'primary'}
            style={this.getNewSaleButtonConfigStyle()}
            icon={<PlusCircleOutlined style={{ marginRight: 15 }} />}
            onClick={() => this.setState({ openModal: true })}
          >
            Nuevo proveedor
          </Button>
        </div>
        <div className="table-container">
          <Table
            columns={columns}
            dataSource={detail}
            style={{ width: '100%' }}
            pagination={{ pageSize: 9 }}
            scroll={{ scrollToFirstRowOnChange: true }}
            size="small"
          />
        </div>
        <Modal
          title={<TitleText2 content={'Nuevo Proveedor'} />}
          width={600}
          footer={[
            <Button
              key={1}
              type={'default'}
              onClick={() => {
                createNotification('warning', 'En desarrollo', 'Esto cancelar toda accin de guardar el proveedor');
                this.setState({ openModal: false })
              }}
            >
              Cancelar
            </Button>,
            <Button
              key={2}
              type={'primary'}
              icon={<SaveOutlined style={{ color: '#FFF' }} />}
              onClick={() => {
                createNotification('success', 'En desarrollo', 'Esto guardar el proveedor');
                this.setState({ openModal: false })
              }}
            >
              Guardar
            </Button>,
          ]}
          centered
          maskClosable={false}
          visible={openModal}
          onOk={() => this.setState({ openModal: false })}
          onCancel={() => this.setState({ openModal: false })}
        >
          <ModalBody width={554}>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Nombre del proveedor:" />
              </div>
              <div className="sec-50">
                <Input placeholder="Distribuidora de Gas: El Pacfico" style={{ width: '100%' }} />
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Contacto:" />
              </div>
              <div className="sec-50">
                <Input placeholder="Manuel Atilio Hernndez Flores" style={{ width: '100%' }} />
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Email:" />
              </div>
              <div className="sec-50">
                <Input placeholder="ventaselpacifico@gmail.com" style={{ width: '100%' }} />
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Telfono:" />
              </div>
              <div className="sec-50">
                <Input placeholder="77208890" style={{ width: '100%' }} name="phone" onChange={this.onChangeInput} />
              </div>
            </div>
          </ModalBody>
        </Modal>
      </Wrapper>
    );
  }
}

// Providers.propTypes = {

// }

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #FFF;
  height: 100%;
  .header {
    margin: 10px 30px;
    width: calc(100% - 60px);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    .dec-line {
      width: 100%;
      margin-left: 30px;
      height: 2px;
      background-color: #2185D0;
    }
  }
  .table-container {
    margin: 10px 30px;
    width: calc(100% - 60px);
    height: 100%;
    .ant-table table { font-size: 12pt; }
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

export default Providers;
