import {
  CloseOutlined,
  ControlOutlined, EditFilled, EnvironmentTwoTone, PlusCircleOutlined, PlusOutlined, SaveOutlined,
} from '@ant-design/icons';
import {
  Button, Input, Select, Table, Tabs, Timeline, List,
} from 'antd';
import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import styled from 'styled-components';
import Modal from 'antd/lib/modal/Modal';

import detail from '../../data/detail';
import { TitleText, TitleText2 } from '../../common/texts/Titles';
import { createNotification } from '../../helpers/notifications';
import { textColumn } from '../../helpers/columnDefinition';
import { NormalText2, NormalText3 } from '../../common/texts/Normal';
import { ModalBody } from '../../common/misc/ModalBody';

const { Option } = Select;
const { TabPane } = Tabs;

class Customers extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
    openModal: false,
  })

  getColumns = () => [
    textColumn('Nombre', 'contact', '25%', 'left'),
    textColumn('Telfonos', 'phone', '25%', 'left'),
    textColumn('Direcciones', 'address', '40%', 'left'),
    {
      title: '',
      dataIndex: 'key',
      width: '10%',
      key: 'key',
      render: value => (
        <div>
          <Button
            style={{ backgroundColor: '#FFF' }}
            type="primary"
            size="small"
            onClick={() => createNotification('info', `En desarrollo ${value}`, 'Funcionalidad en desarrollo')}
            icon={<EditFilled style={{ color: '#000' }} />}
          />
        </div>
      ),
    },
  ]

  getNewSaleButtonConfigStyle = () => ({
    fontSize: '12pt',
    fontWeight: 600,
    marginLeft: 20,
    height: 50,
    width: '200px',
    borderRadius: 5,
    backgroundColor: '#2185D0',
  })

  renderHistoryCard = () => (
    <div className="history-card">
      <div className="sec-1">
        <p className="date">{'Martes, 14/07/2020'}</p>
        <p className="time">{'10:50 a.m.'}</p>
      </div>
      <div className="sec-2">
        <p className="amount">{'3 items'}</p>
        <p className="total">{'$50.50'}</p>
      </div>
    </div>
  )

  getAddressListItems = () => [
    { name: 'Direccin 1', address: '509 S Clay St, Holden, MO, 64040' },
    { name: 'Direccin 2', address: '14 John Johnson Rd, Pitkin, LA, 70656' },
    { name: 'Direccin 3', address: '40304 Ski Park Rd E, Eatonville, WA, 98328' },
    { name: 'Direccin 4', address: '4607 Cleveland Rd, Cohutta, GA, 30710' },
  ]

  getPhoneListItems = () => [
    { name: 'Telfono 1', phone: '7931-0064' },
    { name: 'Telfono 2', phone: '7634-5490' },
    { name: 'Telfono 3', phone: '7222-0233' },
    { name: 'Telfono 4', phone: '7268-6723' },
  ]

  render() {
    const { openModal } = this.state;
    return (
      <Wrapper>
        <div className="customers-cnt">
          <div className="header">
            <TitleText content={'Clientes'} />
            <div className="dec-line" />
            <Button
              type={'primary'}
              style={this.getNewSaleButtonConfigStyle()}
              icon={<PlusCircleOutlined style={{ marginRight: 15 }} />}
              onClick={() => this.setState({ openModal: true })}
            >
              Nuevo cliente
            </Button>
          </div>
          <div className="table-container">
            <Table
              columns={this.getColumns()}
              dataSource={detail}
              style={{ width: '100%' }}
              pagination={{ pageSize: 9 }}
              scroll={{ scrollToFirstRowOnChange: true }}
              size="small"
            />
          </div>
        </div>
        <div className="history-cnt">
          <div style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between',
            alignItems: 'center',
          }}
          >
            <TitleText2 bold content={'Historial'} />
            <div>
              <button
                className="filter-label"
                type="button"
                onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
              >
                Filtrar<ControlOutlined style={{ marginLeft: 5, marginBottom: 5 }} />
              </button>
            </div>
          </div>
          <div style={{ marginTop: 25 }} />
          <Timeline>
            <Timeline.Item color={'#2185D0'}>
              {this.renderHistoryCard()}
            </Timeline.Item>
            <Timeline.Item color={'#2185D0'}>
              {this.renderHistoryCard()}
            </Timeline.Item>
            <Timeline.Item color={'#2185D0'}>
              {this.renderHistoryCard()}
            </Timeline.Item>
            <Timeline.Item color={'#2185D0'}>
              {this.renderHistoryCard()}
            </Timeline.Item>
          </Timeline>
        </div>
        <Modal
          title={<TitleText2 content={'Nuevo Cliente'} />}
          width={920}
          footer={[
            <Button
              key={1}
              type={'default'}
              onClick={() => {
                createNotification('warning', 'En desarrollo', 'Esto cancelar toda accin de guardar al cliente');
                this.setState({ openModal: false })
              }}
            >
              Cancelar
            </Button>,
            <Button
              key={2}
              type={'primary'}
              icon={<SaveOutlined style={{ color: '#FFF' }} />}
              onClick={() => {
                createNotification('success', 'En desarrollo', 'Esto guardar al cliente');
                this.setState({ openModal: false })
              }}
            >
              Guardar
            </Button>,
          ]}
          centered
          maskClosable={false}
          visible={openModal}
          onOk={() => this.setState({ openModal: false })}
          onCancel={() => this.setState({ openModal: false })}
        >
          <ModalBody>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Nombre:" />
              </div>
              <div className="sec-50">
                <Input placeholder="Juan" style={{ width: '100%' }} />
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Direccin en documento:" />
              </div>
              <div className="sec-50">
                <Input placeholder="Calle Las Amapolas" style={{ width: '100%' }} />
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Departamento" />
                <Select
                  showSearch
                  placeholder="Seleccione departamento"
                  optionFilterProp="children"
                  onChange={this.onChange}
                  onSearch={this.onSearch}
                  filterOption={
                  (input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                  style={{ width: '60%' }}
                >
                  <Option value={'Ahuachapn'}>Ahuachapn</Option>
                  <Option value={'Cabaas'}>Cabaas</Option>
                  <Option value={'Chalatenango'}>Chalatenango</Option>
                  <Option value={'Cuscatln'}>Cuscatln</Option>
                  <Option value={'La Libertad'}>La Libertad</Option>
                  <Option value={'La Paz'}>La Paz</Option>
                  <Option value={'La Unin'}>La Unin</Option>
                  <Option value={'Morazn'}>Morazn</Option>
                  <Option value={'San Miguel'}>San Miguel</Option>
                  <Option value={'San Salvador'}>San Salvador</Option>
                  <Option value={'San Vicente'}>San Vicente</Option>
                  <Option value={'Santa Ana'}>Santa Ana</Option>
                  <Option value={'Sonsonate'}>Sonsonate</Option>
                  <Option value={'Usulutn'}>Usulutn</Option>
                </Select>
              </div>
              <div className="sec-25">
                <NormalText2 className="label" content="NIT:" />
                <Input placeholder="0000-000000-000-0" style={{ width: '70%' }} />
              </div>
              <div className="sec-25">
                <NormalText2 className="label" content="NRC:" />
                <Input placeholder="000000-0" style={{ width: '70%' }} />
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Telfono:" />
              </div>
              <div className="sec-50">
                <Input placeholder="7777-7777" style={{ width: '85%' }} />
                <Button
                  style={{ backgroundColor: '#FFF' }}
                  type="primary"
                  size="small"
                  onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
                  icon={<PlusOutlined style={{ color: '#000' }} />}
                />
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Direccin:" />
              </div>
              <div className="sec-50">
                <Input placeholder="Calle Melancola" style={{ width: '85%' }} />
                <Button
                  style={{ backgroundColor: '#FFF' }}
                  type="primary"
                  size="small"
                  onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
                  icon={<PlusOutlined style={{ color: '#000' }} />}
                />
              </div>
            </div>
            <div className="item">
              <Tabs defaultActiveKey="1" onChange={(e) => console.log(e)} style={{ width: '100%' }}>
                <TabPane tab="Direcciones" key="1">
                  <List
                    header={(
                      <div className="listitem">
                        <div className="sec-25">
                          <NormalText3 bold content={'Nombre'} />
                        </div>
                        <div className="sec-50">
                          <NormalText3 bold content={'Direccin'} />
                        </div>
                        <div className="sec-15">
                          <NormalText3 bold content={'Usar Mapa'} />
                        </div>
                        <div className="sec-10">
                          <NormalText3 bold content={''} />
                        </div>
                      </div>
                    )}
                    bordered
                    dataSource={this.getAddressListItems()}
                    renderItem={item => (
                      <List.Item key={item.name}>
                        <div className="listitem">
                          <div className="sec-25">
                            <NormalText3 content={item.name} />
                          </div>
                          <div className="sec-50">
                            <NormalText3 content={item.address} />
                          </div>
                          <div className="sec-15">
                            <Button
                              style={{ backgroundColor: '#FFF', border: 'none' }}
                              type="primary"
                              size="small"
                              onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
                              icon={<EnvironmentTwoTone twoToneColor="#3EA2E5" />}
                            />
                          </div>
                          <div className="sec-10-cent">
                            <Button
                              style={{ backgroundColor: '#FFF', border: 'none' }}
                              type="primary"
                              size="small"
                              onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
                              icon={<CloseOutlined style={{ color: '#FF6565' }} />}
                            />
                          </div>
                        </div>
                      </List.Item>
                    )}
                  />
                </TabPane>
                <TabPane tab="Telfonos" key="2">
                  <List
                    header={(
                      <div className="listitem">
                        <div className="sec-30">
                          <NormalText3 bold content={'Nombre'} />
                        </div>
                        <div className="sec-60">
                          <NormalText3 bold content={'Telfono'} />
                        </div>
                        <div className="sec-10">
                          <NormalText3 bold content={''} />
                        </div>
                      </div>
                    )}
                    bordered
                    dataSource={this.getPhoneListItems()}
                    renderItem={item => (
                      <List.Item key={item.name}>
                        <div className="listitem">
                          <div className="sec-30">
                            <NormalText3 content={item.name} />
                          </div>
                          <div className="sec-60">
                            <NormalText3 content={item.phone} />
                          </div>
                          <div className="sec-10">
                            <Button
                              style={{ backgroundColor: '#FFF', border: 'none' }}
                              type="primary"
                              size="small"
                              onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
                              icon={<CloseOutlined style={{ color: '#FF6565' }} />}
                            />
                          </div>
                        </div>
                      </List.Item>
                    )}
                  />
                </TabPane>
              </Tabs>
            </div>
          </ModalBody>
        </Modal>
      </Wrapper>
    );
  }
}

// Customers.propTypes = {

// }

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  min-height: calc(100vh - 75px);
  .customers-cnt, .history-cnt {
    margin: 30px;
  }
  .customers-cnt {
    background-color: white;
    width: calc(70% - 80px);
    height: calc(100vh - 120px);
    box-shadow: 3px 3px 10px 1px #DDDDDD;
    .header {
      margin: 10px 30px;
      width: calc(100% - 60px);
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      .dec-line {
        width: 100%;
        margin-left: 30px;
        height: 2px;
        background-color: #2185D0;
      }
    }
    .table-container {
      margin: 10px 30px;
      width: calc(100% - 60px);
      .ant-table table { font-size: 12pt; }
    }
  }
  .history-cnt {
    display: flex;
    flex-direction: column;
    width: calc(30% - 60px);
    .filter-label {
      padding: 0px;
      border: none;
      outline: none;
      background-color: transparent;
      font-size: 13px;
      :hover {
        color: #2185D0;
        cursor: pointer;
      }
    }
    .history-card {
      background-color: #FFF;
      border-bottom-left-radius: 15px;
      border-top-right-radius: 15px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 10px;
      .sec-1, .sec-2 {
        p { margin: 0px; font-size: 11pt; }
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .date, .total { font-weight: 600 }
      }
    }
    .ant-steps-item-content {
      background-color: white;
      border-radius: 10px;
      margin-top: 5px;
    }
    .ant-timeline-item-tail {
      border-left: 2px solid #2185D0;
    }
    .ant-timeline-item-head {
      background-color: #2185D0;
    }
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

export default Customers;
