import { connect } from 'react-redux';

import { goTo, getMethod } from '../../store/actions/globalActn';
import Inventory from '../components/Inventory';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  goTo: (path) => dispatch(goTo(path)),
  getMethod: (path) => dispatch(getMethod(path)),
})

const InventoryCnt = connect(mapStateToProps, mapDispatchToProps)(Inventory);
export default InventoryCnt;
