import { connect } from 'react-redux';

import { getMethod } from '../../../store/actions/globalActn';
import Products from '../../components/InventorySections/Products';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const ProductsCnt = connect(mapStateToProps, mapDispatchToProps)(Products);
export default ProductsCnt;
