import { connect } from 'react-redux';

import { getMethod } from '../../../store/actions/globalActn';
import Brands from '../../components/InventorySections/Brands';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const BrandsCnt = connect(mapStateToProps, mapDispatchToProps)(Brands);
export default BrandsCnt;
