import {
  GO_TO,
  GET_SUCCESS,
  GET_REJECTED,
  POST_SUCCESS,
  POST_REJECTED,
  PUT_SUCCESS,
  PUT_REJECTED,
  DELETE_SUCCESS,
  DELETE_REJECTED,
} from '../actionTypes';

const initialState = {
  serverMessage: {},
}

const globalRdcr = (state = initialState, action) => {
  switch (action.type) {
    case GO_TO:
      return {
        ...state,
      }
    case GET_SUCCESS:
    case GET_REJECTED:
    case POST_SUCCESS:
    case POST_REJECTED:
    case PUT_SUCCESS:
    case PUT_REJECTED:
    case DELETE_SUCCESS:
    case DELETE_REJECTED:
      return {
        ...state,
        serverMessage: action.payload,
      };
    default:
      return state;
  }
}

export default globalRdcr;
