import { EditFilled, PlusCircleOutlined, SaveOutlined } from '@ant-design/icons';
import {
  Button, Input, Radio, Select, Table,
} from 'antd';
import Modal from 'antd/lib/modal/Modal';
import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import styled from 'styled-components';

import { ModalBody } from '../../../common/misc/ModalBody';
import { NormalText2 } from '../../../common/texts/Normal';
import { TitleText, TitleText2 } from '../../../common/texts/Titles';
import detail from '../../../data/detail';
import { textColumn } from '../../../helpers/columnDefinition';
import { createNotification } from '../../../helpers/notifications';

const { Option } = Select;

class Products extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
    openModal: false,
    productType: 1,
  })

  getNewSaleButtonConfigStyle = () => ({
    fontSize: '12pt',
    fontWeight: 600,
    marginLeft: 20,
    height: 50,
    width: '200px',
    borderRadius: 5,
    backgroundColor: '#2185D0',
  })

  getColumns = () => [
    textColumn('Cantidad', 'amount', '10%', 'center', '', ''),
    textColumn('Llenas', 'amount', '8%', 'center', '', ''),
    textColumn('Vacas', 'amount', '8%', 'center', '', ''),
    textColumn('Producto', 'descrip', '44%', 'left', '', ''),
    textColumn('Marca', 'brand', '10%', 'left', '', ''),
    textColumn('Medida', 'measurement', '10%', 'left', '', ''),
    {
      title: '',
      dataIndex: 'key',
      width: '10%',
      key: 'key',
      render: value => (
        <div>
          <Button
            style={{ backgroundColor: '#FFF' }}
            type="primary"
            size="small"
            onClick={() => createNotification('info', `En desarrollo ${value}`, 'Funcionalidad en desarrollo')}
            icon={<EditFilled style={{ color: '#000' }} />}
          />
        </div>
      ),
    },
  ]

  onChangeProductType = (e) => {
    const { value } = e.target;
    this.setState({ productType: value });
  }

  render() {
    const { openModal, productType } = this.state;
    return (
      <Wrapper>
        <div className="header">
          <TitleText content={'Productos'} />
          <div className="dec-line" />
          <Button
            type={'primary'}
            style={this.getNewSaleButtonConfigStyle()}
            icon={<PlusCircleOutlined style={{ marginRight: 15 }} />}
            onClick={() => this.setState({ openModal: true })}
          >
            Nuevo producto
          </Button>
        </div>
        <div className="table-container">
          <Table
            columns={this.getColumns()}
            dataSource={detail}
            style={{ width: '100%' }}
            pagination={{ pageSize: 9 }}
            scroll={{ scrollToFirstRowOnChange: true }}
            size="small"
          />
        </div>
        <Modal
          title={<TitleText2 content={'Nuevo Producto'} />}
          width={600}
          footer={[
            <Button
              key={1}
              type={'default'}
              onClick={() => {
                createNotification('warning', 'En desarrollo', 'Esto cancelar toda accin de guardar el producto');
                this.setState({ openModal: false })
              }}
            >
              Cancelar
            </Button>,
            <Button
              key={2}
              type={'primary'}
              icon={<SaveOutlined style={{ color: '#FFF' }} />}
              onClick={() => {
                createNotification('success', 'En desarrollo', 'Esto guardar el producto');
                this.setState({ openModal: false })
              }}
            >
              Guardar
            </Button>,
          ]}
          centered
          maskClosable={false}
          visible={openModal}
          onOk={() => this.setState({ openModal: false })}
          onCancel={() => this.setState({ openModal: false })}
        >
          <ModalBody width={554}>
            <div className="item">
              <div className="sec-25">
                <Radio style={{ fontSize: '12pt' }} value={1} checked={productType === 1} onChange={this.onChangeProductType}>Cilindro</Radio>
              </div>
              <div className="sec-25">
                <Radio style={{ fontSize: '12pt' }} value={2} checked={productType === 2} onChange={this.onChangeProductType}>Artculo</Radio>
              </div>
            </div>
            <div className="item">
              <div className="sec-25">
                <NormalText2 className="label" content="Nombre:" />
              </div>
              <div className="sec-75">
                <Input placeholder="Cilindro de 25 Libras" style={{ width: '100%' }} />
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Marca:" />
                <Select
                  showSearch
                  placeholder="Seleccione una medida"
                  optionFilterProp="children"
                  onChange={this.onChange}
                  onSearch={this.onSearch}
                  filterOption={
                  (input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                  style={{ width: '75%' }}
                >
                  <Option value={'Ahuachapn'}>N/A</Option>
                  <Option value={'Cabaas'}>Genrica</Option>
                  <Option value={'Chalatenango'}>Ninguna</Option>
                </Select>
              </div>
              <div className="sec-50">
                <NormalText2 className="label" content="Medida:" />
                <Select
                  showSearch
                  disabled={productType !== 1}
                  placeholder="Seleccione una medida"
                  optionFilterProp="children"
                  onChange={this.onChange}
                  onSearch={this.onSearch}
                  filterOption={
                  (input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                  style={{ width: '75%' }}
                >
                  <Option value={'Ahuachapn'}>Ahuachapn</Option>
                  <Option value={'Cabaas'}>Cabaas</Option>
                  <Option value={'Chalatenango'}>Chalatenango</Option>
                </Select>
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Precio:" />
                <Input type={'number'} placeholder="00.00" style={{ width: '75%' }} />
              </div>
              <div className="sec-50">
                <NormalText2 className="label" content="Precio con subsidio:" />
                <Input type={'number'} placeholder="00.00" style={{ width: '40%' }} disabled={productType !== 1} />
              </div>
            </div>
          </ModalBody>
        </Modal>
      </Wrapper>
    );
  }
}

// Products.propTypes = {

// }

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #FFF;
  height: 100%;
  .header {
    margin: 10px 30px;
    width: calc(100% - 60px);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    .dec-line {
      width: 100%;
      margin-left: 30px;
      height: 2px;
      background-color: #2185D0;
    }
  }
  .table-container {
    margin: 10px 30px;
    width: calc(100% - 60px);
    height: 100%;
    .ant-table table { font-size: 12pt; }
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

export default Products;
