import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components'

import { TitleText, TitleText2 } from '../texts/Titles';
import { NormalText } from '../texts/Normal';

export const SaleListCard = ({
  number = 1,
  date = 'Hoy, 11:00am',
  customer = 'Gustavo Snchez',
  amount = 13,
  total = 25.56,
}) => (
  <SaleListCardBody>
    <div className="top">
      <TitleText bold content={`#${number}`} />
      <NormalText content={`${date}`} />
    </div>
    <div className="middle">
      <TitleText2 bold content={`${customer}`} />
    </div>
    <div className="bottom">
      <NormalText content={`${amount} items`} />
      <TitleText bold content={`$${+total.toFixed(2)}`} />
    </div>
  </SaleListCardBody>
)

SaleListCard.propTypes = {
  number: PropTypes.number,
  date: PropTypes.string,
  customer: PropTypes.string,
  amount: PropTypes.number,
  total: PropTypes.number,
}

SaleListCard.defaultProps = {
  number: 1,
  date: 'Hoy, 11:00am',
  customer: 'Gustavo Snchez',
  amount: 13,
  total: 25.56,
}

const SaleListCardBody = styled.div`
  background-color: #FFF;
  min-height: 185px;
  max-height: 185px;
  width: calc(100% - 3px);
  border-top: 2px solid #F2F2F2;
  border-bottom: 2px solid #F2F2F2;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  transition: 0.3s;
  .top, .bottom, .middle {
    margin: 0px 20px;
    width: calc(100% - 40px);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .top {
    align-items: flex-start;
  }
  .middle {
    align-items: center;
  }
  .bottom {
    align-items: flex-end;
  }
  :hover {
    background-color: #F2F2F2;
    cursor: pointer;
  }
`
