import React from 'react';

export const textColumn = (
  title = 'Column', dataindexkey = 'column', width = '10%', align = 'left', prefix = '', suffix = '',
) => ({
  title,
  dataIndex: dataindexkey,
  key: dataindexkey,
  width,
  ellipsis: true,
  align,
  render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${prefix}${value}${suffix}`}</p>,
})

export const twoDecimalColumn = (
  title = 'Column', dataindexkey = 'column', width = '10%', align = 'left', prefix = '', suffix = '',
) => ({
  title,
  dataIndex: dataindexkey,
  key: dataindexkey,
  width,
  ellipsis: true,
  align,
  render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${prefix}${value.toFixed(2)}${suffix}`}</p>,
})
