import { EditFilled, PlusCircleOutlined, SaveOutlined } from '@ant-design/icons';
import { Button, Input, Table } from 'antd';
import Modal from 'antd/lib/modal/Modal';
import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import styled from 'styled-components';

import { ModalBody } from '../../../common/misc/ModalBody';
import { NormalText2 } from '../../../common/texts/Normal';
import { TitleText, TitleText2 } from '../../../common/texts/Titles';
import detail from '../../../data/detail';
import { textColumn } from '../../../helpers/columnDefinition';
import { createNotification } from '../../../helpers/notifications';

class Measurements extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
    openModal: false,
  })

  getNewSaleButtonConfigStyle = () => ({
    fontSize: '12pt',
    fontWeight: 600,
    marginLeft: 20,
    height: 50,
    width: '200px',
    borderRadius: 5,
    backgroundColor: '#2185D0',
  })

  getColumns = () => [
    textColumn('Nombre', 'measurement', '30%', 'left', '', ''),
    textColumn('Descripcion', 'branddescrip', '60%', 'left', '', ''),
    {
      title: '',
      dataIndex: 'key',
      width: '10%',
      key: 'key',
      render: value => (
        <div>
          <Button
            style={{ backgroundColor: '#FFF' }}
            type="primary"
            size="small"
            onClick={() => createNotification('info', `En desarrollo ${value}`, 'Funcionalidad en desarrollo')}
            icon={<EditFilled style={{ color: '#000' }} />}
          />
        </div>
      ),
    },
  ]

  render() {
    const { openModal } = this.state;
    return (
      <Wrapper>
        <div className="header">
          <TitleText content={'Medidas'} />
          <div className="dec-line" />
          <Button
            type={'primary'}
            style={this.getNewSaleButtonConfigStyle()}
            icon={<PlusCircleOutlined style={{ marginRight: 15 }} />}
            onClick={() => this.setState({ openModal: true })}
          >
            Nueva medida
          </Button>
        </div>
        <div className="table-container">
          <Table
            columns={this.getColumns()}
            dataSource={detail}
            style={{ width: '100%' }}
            pagination={{ pageSize: 9 }}
            scroll={{ scrollToFirstRowOnChange: true }}
            size="small"
          />
        </div>
        <Modal
          title={<TitleText2 content={'Nueva Medida'} />}
          width={600}
          footer={[
            <Button
              key={1}
              type={'default'}
              onClick={() => {
                createNotification('warning', 'En desarrollo', 'Esto cancelar toda accin de guardar la medida');
                this.setState({ openModal: false })
              }}
            >
              Cancelar
            </Button>,
            <Button
              key={2}
              type={'primary'}
              icon={<SaveOutlined style={{ color: '#FFF' }} />}
              onClick={() => {
                createNotification('success', 'En desarrollo', 'Esto guardar la medida');
                this.setState({ openModal: false })
              }}
            >
              Guardar
            </Button>,
          ]}
          centered
          maskClosable={false}
          visible={openModal}
          onOk={() => this.setState({ openModal: false })}
          onCancel={() => this.setState({ openModal: false })}
        >
          <ModalBody width={554}>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Nombre de la medida:" />
              </div>
              <div className="sec-50">
                <Input placeholder="25 libras" style={{ width: '100%' }} />
              </div>
            </div>
            <div className="item">
              <div className="sec-50">
                <NormalText2 className="label" content="Descripcin:" />
              </div>
              <div className="sec-50">
                <Input placeholder="Medida cilindro pequeo" style={{ width: '100%' }} />
              </div>
            </div>
          </ModalBody>
        </Modal>
      </Wrapper>
    );
  }
}

// Measurements.propTypes = {

// }

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #FFF;
  height: 100%;
  .header {
    margin: 10px 30px;
    width: calc(100% - 60px);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    .dec-line {
      width: 100%;
      margin-left: 30px;
      height: 2px;
      background-color: #2185D0;
    }
  }
  .table-container {
    margin: 10px 30px;
    width: calc(100% - 60px);
    height: 100%;
    .ant-table table { font-size: 12pt; }
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

export default Measurements;
