import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

export const ModalBody = ({ children, width = 874 }) => (
  <ModalBodyC width={width}>
    {children}
  </ModalBodyC>
)

ModalBody.propTypes = {
  width: PropTypes.number,
  // eslint-disable-next-line react/forbid-prop-types
  children: PropTypes.any,
}

ModalBody.defaultProps = {
  width: 874,
  children: <div />,
}

const ModalBodyC = styled.div`
  width: ${props => (`${props.width}px`)};
  .item {
    margin: 10px 0px;
  }
  .listitem {
    margin: 2px 0px;
  }
  .item, .listitem {
    width: calc(100%);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    p { margin: 0px; }
    .sec-10, .sec-25, .sec-30, .sec-50, .sec-60, .sec-75 {
      margin: 0px 5px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      .ant-input {
        border: none;
        border-radius: 0px;
        border-bottom: 1px solid #d9d9d9;
      }
      .ant-input:focus {
        border: none;
        border-bottom: 1px solid #2185D0 !important;
        box-shadow: none;
      }
      .ant-select-selector {
        border: none;
        border-radius: 0px;
        border-bottom: 1px solid #d9d9d9;
      }
      .ant-select-focused .ant-select-selector,
      .ant-select-selector:focus,
      .ant-select-selector:active,
      .ant-select-open .ant-select-selector {
        border-color: #d9d9d9 !important;
        border-bottom: 1px solid #2185D0 !important;
        box-shadow: none !important;
      }
    }
    .sec-10 {
      width: calc(10% - 10px);
    }
    .sec-15 {
      width: calc(15% - 10px);
    }
    .sec-25 {
      width: calc(25% - 10px);
    }
    .sec-30 {
      width: calc(30% - 10px);
    }
    .sec-50 {
      width: calc(50% - 10px);
    }
    .sec-60 {
      width: calc(60% - 10px);
    }
    .sec-75 {
      width: calc(75% - 10px);
    }
    .ant-list-header, .ant-list-item, .ant-list-pagination {
      padding: 0px 10px;
      margin: 5px;
    }
  }
`
