import {
  CalendarOutlined, ClockCircleOutlined, CloseOutlined,
  CopyOutlined, EditOutlined, PhoneFilled, PlusCircleOutlined,
} from '@ant-design/icons';
import {
  Button, Select, Tooltip, Table, Radio,
} from 'antd';
import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import styled from 'styled-components';

import { createNotification } from '../../helpers/notifications';
import detailData from '../../data/detail';
import { SaleListCard } from '../../common/cards/SaleListCard';
import { NormalText, NormalText2 } from '../../common/texts/Normal';
import { TitleText, TitleText2 } from '../../common/texts/Titles';
import { textColumn, twoDecimalColumn } from '../../helpers/columnDefinition';

const { Option } = Select;

class Sales extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
    localCheck: true,
    showSaleDetail: false,
    deliverCheck: false,
  })

  getNewSaleButtonConfigStyle = () => ({
    fontSize: '28pt',
    fontWeight: 600,
    height: 90,
    width: '100%',
    borderRadius: 5,
    backgroundColor: '#2185D0',
  })

  getColumns = () => [
    textColumn('Cantidad', 'amount', '12.50%', 'center', '', ''),
    textColumn('Descripcin', 'descrip', '45%', 'left', '', ''),
    twoDecimalColumn('Unidad ($)', 'unitprice', '15%', 'left', '', ''),
    twoDecimalColumn('Total ($)', 'total', '15%', 'left', '', ''),
  ]

  onChange = (value) => {
    console.log(`selected ${value}`);
  }

  onSearch = (val) => {
    console.log('search:', val);
  }

  onChange1 = (value) => {
    console.log(`selected ${value}`);
    this.setState({ address: value });
  }

  onSearch1 = (val) => {
    console.log('search:', val);
  }

  render() {
    const { address, localCheck, deliverCheck } = this.state;
    return (
      <Wrapper>
        <div className="sales-list-cnt">
          <Button
            type={'primary'}
            style={this.getNewSaleButtonConfigStyle()}
            icon={<PlusCircleOutlined style={{ marginRight: 15 }} />}
            onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
          >
            Nueva venta
          </Button>
          <div className="sales-list">
            <SaleListCard />
            <SaleListCard />
            <SaleListCard />
            <SaleListCard />
          </div>
        </div>
        <div className="sales-details-cnt">
          <div className="details-cnt">
            <div className="close-btn-cnt">
              <Button style={{ border: 'none', backgroundColor: '#FFF' }} icon={<CloseOutlined style={{ fontSize: '24px' }} />} />
            </div>
            <div className="header-sale">
              <div className="order-id">
                <TitleText content={'Venta #11'} />
                <button
                  className="copy-label"
                  type="button"
                  onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
                >
                  Copiar ID<CopyOutlined style={{ marginLeft: 5, marginBottom: 5 }} />
                </button>
              </div>
              <div>
                <div style={{
                  display: 'flex', flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end',
                }}
                >
                  <CalendarOutlined style={{ marginRight: 10 }} />
                  <NormalText2 content={'Martes, 19/06/2020'} />
                </div>
                <div style={{
                  display: 'flex', flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end',
                }}
                >
                  <ClockCircleOutlined style={{ marginRight: 10 }} />
                  <NormalText2 content={'10:50 a.m.'} />
                </div>
              </div>
            </div>
            <div className="header-sale-customer">
              <Select
                showSearch
                bordered={false}
                placeholder="Cliente"
                optionFilterProp="children"
                onChange={this.onChange}
                onSearch={this.onSearch}
                filterOption={
                  (input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                style={{ width: '50%', fontSize: 24, fontWeight: 500 }}
                size={'large'}
              >
                <Option value={1}>Nidia Daz</Option>
                <Option value={2}>Salvador Snchez Cern</Option>
                <Option value={3} disabled>
                  Mauricio Funes
                </Option>
                <Option value={4}>Nayib Bukele</Option>
              </Select>
              <div className="order-phonenumber">
                <div style={{
                  display: 'flex', flexDirection: 'row', alignItems: 'center', justifyContent: 'flex-end',
                }}
                >
                  <PhoneFilled style={{ marginRight: 10 }} />
                  <NormalText2 content={'7902-9999'} />
                </div>
              </div>
            </div>
            <div className="header-sale-address">
              <Tooltip
                placement="bottom"
                title={address}
              >
                <Select
                  showSearch
                  bordered={false}
                  placeholder="Direccin"
                  optionFilterProp="children"
                  onChange={this.onChange1}
                  onSearch={this.onSearch1}
                  filterOption={
                    (
                      input,
                      option,
                    ) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                  style={{
                    width: '50%', fontSize: 18,
                  }}
                  size={'large'}
                >
                  <Option
                    value={`Avenida siempreviva 742, ex estadio barraza,
                    Barrio La Cruz, San Miguel, San Miguel, local 503`}
                  >
                    Avenida siempreviva 742, ex estadio barraza,
                    Barrio La Cruz, San Miguel, San Miguel, local 503
                  </Option>
                  <Option value={2}>Salvador Snchez Cern</Option>
                  <Option value={3} disabled>
                    Mauricio Funes
                  </Option>
                  <Option value={4}>Nayib Bukele</Option>
                </Select>
              </Tooltip>
            </div>
            <div className="header-sale-amounts">
              <NormalText content={'Recibidos: $30.00'} />
              <NormalText content={'Cambio: $0.50'} />
              <TitleText2 bold content={'Total: $27.49'} />
            </div>
            <div className="header-sale-table">
              <Table
                columns={this.getColumns()}
                dataSource={detailData}
                style={{ width: '100%' }}
                pagination={false}
                scroll={{ y: 170 }}
                size="small"
              />
            </div>
            <div className="header-sale-footer">
              <div>
                <Radio value={1} checked={localCheck}>Venta local</Radio>
                <Radio value={2} checked={deliverCheck}>Envo</Radio>
              </div>
              <div>
                <Button
                  style={{ backgroundColor: '#FFF', margin: '0px 15px' }}
                  icon={<EditOutlined style={{ color: '#2185D0' }} />}
                  onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
                >
                  Editar
                </Button>
              </div>
            </div>
          </div>
        </div>
      </Wrapper>
    );
  }
}

// Sales.propTypes = {

// }

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  min-height: calc(100vh - 60px);
  width: 100%;
  .sales-list-cnt {
    margin: 30px;
    width: calc(45% - 60px);
  }
  .sales-details-cnt {
    position: relative;
    margin: 30px;
    width: calc(55% - 80px);
  }
  .sales-list-cnt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .sales-list {
      margin: 20px 0px 0px 0px;
      width: 100%;
      min-height: calc(100vh - 230px);
      max-height: calc(100vh - 230px);
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      overflow-y: scroll;
      ::-webkit-scrollbar {
        width: 8px;
        height: 3px;
      }
      ::-webkit-scrollbar-track {
        border-radius: 3px;
      }
      ::-webkit-scrollbar-thumb {
        background: #D5D5D5;
        width: 8px;
        height: 3px;
        border-radius: 3px;
      }
    }
  }
  .sales-details-cnt {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-color: white;
    min-height: calc(100vh - 120px);
    max-height: calc(100vh - 120px);
    box-shadow: 3px 3px 10px 1px #DDDDDD;
    .details-cnt {
      margin: 10px 20px;
      width: calc(100% - 40px);
      .close-btn-cnt {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
      }
      .header-sale {
        margin-top: 0px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
        .order-id {
          margin-left: 10px;
          .copy-label {
            padding: 0px;
            border: none;
            outline: none;
            background-color: transparent;
            font-size: 13px;
            :hover {
              color: #2185D0;
              cursor: pointer;
            }
          }
        }
      }
      .header-sale-customer {
        margin-top: 5px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
        .order-phonenumber {
          text-align: right;
          .phonenumber {
            font-size: 16px;
            margin: 0px;
          }
        }
      }
      .header-sale-address {
        margin-top: 5px;
        display: flex;
        flex-direction: row;
        justify-content: start;
        align-items: flex-start;
      }
      .header-sale-amounts {
        margin-top: 5px;
        margin-left: 10px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
      }
      .header-sale-table {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: center;
        .ant-table table { font-size: 12pt; }
      }
      .header-sale-footer {
        position: absolute;
        bottom: 0;
        margin-bottom: 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: calc(100% - 40px);
        .ant-radio-checked .ant-radio-inner{
          border-color: #4AD991 !important ;
        }

        .ant-radio-checked .ant-radio-inner:after{
          background-color: #4AD991;
        }

        .ant-radio:hover .ant-radio-inner {
          border-color: #4AD991 ;
        }
      }
    }
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

export default Sales;
