import React, { Component } from 'react';
import { Route, Switch } from 'react-router';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import ProductsCnt from '../container/InventorySections/ProductsCnt';
import StocksCnt from '../container/InventorySections/StocksCnt';
import BrandsCnt from '../container/InventorySections/BrandsCnt';
import MeasurementsCnt from '../container/InventorySections/MeasurementsCnt';
import PurchasesCnt from '../container/InventorySections/PurchasesCnt';
import ProvidersCnt from '../container/InventorySections/ProvidersCnt';

class Inventory extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.defineMenuSelectedPath(this.props.location.pathname)
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
    menuIndex: 1,
  })

  defineMenuSelectedPath = (path) => {
    switch (path) {
      case '/inventory': this.setState({ menuIndex: 1 }); break;
      case '/inventory/products': this.setState({ menuIndex: 2 }); break;
      case '/inventory/brands': this.setState({ menuIndex: 3 }); break;
      case '/inventory/measurements': this.setState({ menuIndex: 4 }); break;
      case '/inventory/purchases': this.setState({ menuIndex: 5 }); break;
      case '/inventory/providers': this.setState({ menuIndex: 6 }); break;
      default: this.setState({ menuIndex: 1 }); break;
    }
  }

  goToNClose = (path, index) => {
    const { goTo } = this.props;
    this.setState({ menuIndex: index });
    goTo(path);
  }

  renderMenuOption = (index, title, path) => {
    const { menuIndex } = this.state;
    return (
      <MenuOption isSelected={index === menuIndex} onClick={() => this.goToNClose(path, index)}>
        <p>{title}</p>
      </MenuOption>
    )
  }

  render() {
    return (
      <Wrapper>
        <div className="options-cnt">
          {this.renderMenuOption(1, 'Existencias', '/inventory')}
          {this.renderMenuOption(2, 'Productos', '/inventory/products')}
          {this.renderMenuOption(3, 'Marcas', '/inventory/brands')}
          {this.renderMenuOption(4, 'Medidas', '/inventory/measurements')}
          {this.renderMenuOption(5, 'Compras', '/inventory/purchases')}
          {this.renderMenuOption(6, 'Proveedores', '/inventory/providers')}
        </div>
        <div className="option-view-cnt">
          <Switch>
            <Route
              exact
              path="/inventory"
              component={StocksCnt}
            />
            <Route
              exact
              path="/inventory/products"
              component={ProductsCnt}
            />
            <Route
              exact
              path="/inventory/brands"
              component={BrandsCnt}
            />
            <Route
              exact
              path="/inventory/measurements"
              component={MeasurementsCnt}
            />
            <Route
              exact
              path="/inventory/purchases"
              component={PurchasesCnt}
            />
            <Route
              exact
              path="/inventory/providers"
              component={ProvidersCnt}
            />
          </Switch>
        </div>
      </Wrapper>
    );
  }
}

Inventory.propTypes = {
  goTo: PropTypes.func.isRequired,
  location: PropTypes.instanceOf(Object).isRequired,
}

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  min-height: calc(100vh - 75px);
  .options-cnt, .option-view-cnt {
    background-color: white;
    box-shadow: 3px 3px 10px 1px #DDDDDD;
    margin: 30px;
  }
  .options-cnt {
    display: flex;
    flex-direction: column;
    width: calc(25% - 60px);
  }
  .option-view-cnt {
    width: calc(75% - 80px);
    height: calc(100vh - 120px);
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

const MenuOption = styled.div`
  margin-top: -1px;
  width: calc(100%);
  display: flex;
  min-height: 58px;
  flex-direction: row;
  align-items: center;
  border: 1px gray solid;
  outline: none;
  background-color: ${props => (props.isSelected ? '#2185D0' : '#FFF')};
  color: ${props => (props.isSelected ? '#FFF' : '#000')};
  transition: 0.2s;
  p {
    margin: 0px;
    margin-left: 15px;
    font-size: 14pt;
    font-weight: 600;
  }
  :hover {
    cursor: pointer;
    color: #FFFFFF;
    background-color: #2185D0;
  }
`

export default Inventory;
