import axios from 'axios';

import { history } from '../store';
import {
  GO_TO,
  GET_SUCCESS,
  GET_REJECTED,
  POST_SUCCESS,
  POST_REJECTED,
  PUT_SUCCESS,
  PUT_REJECTED,
  DELETE_SUCCESS,
  DELETE_REJECTED,
} from '../actionTypes';

const baseroute = '/';

// FUNC PARA CAMBIO DE ESTADO
const goToFunc = () => ({ type: GO_TO });

const disGetSuccess = (payload) => ({ type: GET_SUCCESS, payload });
const disGetRefused = (payload) => ({ type: GET_REJECTED, payload });
const disPostSuccess = (payload) => ({ type: POST_SUCCESS, payload });
const disPostRejected = (payload) => ({ type: POST_REJECTED, payload });
const disPutSuccess = (payload) => ({ type: PUT_SUCCESS, payload });
const disPutRejected = (payload) => ({ type: PUT_REJECTED, payload });
const disDeleteSuccess = (payload) => ({ type: DELETE_SUCCESS, payload });
const disDeleteRejected = (payload) => ({ type: DELETE_REJECTED, payload });

export const goTo = (path) => (dispatch) => {
  history.push(path);
  dispatch(goToFunc());
}

// FUNC DISPATCH
export const getMethod = () => async (dispatch) => {
  await axios.get(`${baseroute}`).then(async (response) => {
    dispatch(disGetSuccess(response.data));
  }).catch((error) => {
    dispatch(disGetRefused(error));
  })
}

export const postMethod = (bodyData) => async (dispatch) => {
  await axios.get(`${baseroute}`, bodyData).then(async (response) => {
    dispatch(disPostSuccess(response.data));
  }).catch((error) => {
    dispatch(disPostRejected(error));
  })
}

export const putMethod = (bodyData) => async (dispatch) => {
  await axios.get(`${baseroute}`, bodyData).then(async (response) => {
    dispatch(disPutSuccess(response.data));
  }).catch((error) => {
    dispatch(disPutRejected(error));
  })
}

export const deleteMethod = (id) => async (dispatch) => {
  await axios.delete(`${baseroute}/${id}`).then(async (response) => {
    dispatch(disDeleteSuccess(response.data));
  }).catch((error) => {
    dispatch(disDeleteRejected(error));
  })
}
