import { EditFilled } from '@ant-design/icons';
import { Button, Table } from 'antd';
import Modal from 'antd/lib/modal/Modal';
import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import styled from 'styled-components';

import { TitleText } from '../../../common/texts/Titles';
import detail from '../../../data/detail';
import { textColumn } from '../../../helpers/columnDefinition';
import { createNotification } from '../../../helpers/notifications';

class Stocks extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
    showEditModal: false,
  })

  getColumns = () => [
    textColumn('Cantidad', 'amount', '10%', 'center', '', ''),
    textColumn('Llenas', 'amount', '8%', 'center', '', ''),
    textColumn('Vacas', 'amount', '8%', 'center', '', ''),
    textColumn('Producto', 'descrip', '44%', 'left', '', ''),
    textColumn('Marca', 'brand', '10%', 'left', '', ''),
    textColumn('Medida', 'measurement', '10%', 'left', '', ''),
    {
      title: '',
      dataIndex: 'key',
      width: '10%',
      key: 'key',
      render: value => (
        <div>
          <Button
            style={{ backgroundColor: '#FFF' }}
            type="primary"
            size="small"
            onClick={
              () => {
                createNotification('info', `Selected id: ${value}`, 'Funcionalidad en desarrollo');
                this.setState({ showEditModal: true })
              }
            }
            icon={<EditFilled style={{ color: '#000' }} />}
          />
        </div>
      ),
    },
  ]

  render() {
    const { showEditModal } = this.state;
    return (
      <Wrapper>
        <div className="header">
          <TitleText content={'Existencias'} />
          <div className="dec-line" />
        </div>
        <div className="table-container">
          <Table
            columns={this.getColumns()}
            dataSource={detail}
            style={{ width: '100%' }}
            pagination={{ pageSize: 9 }}
            scroll={{ scrollToFirstRowOnChange: true }}
            size="small"
          />
        </div>
        <Modal
          visible={showEditModal}
          centered
          onCancel={() => this.setState({ showEditModal: false })}
        >
          <p>Some contents...</p>
          <p>Some contents...</p>
          <p>Some contents...</p>
        </Modal>
      </Wrapper>
    );
  }
}

// Stocks.propTypes = {

// }

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  background-color: #FFF;
  height: 100%;
  .header {
    margin: 10px 30px;
    width: calc(100% - 60px);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    .dec-line {
      width: 100%;
      margin-left: 30px;
      height: 2px;
      background-color: #2185D0;
    }
  }
  .table-container {
    margin: 10px 30px;
    width: calc(100% - 60px);
    height: 100%;
    .ant-table table { font-size: 12pt; }
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

export default Stocks;
