import { connect } from 'react-redux';

import { getMethod } from '../../store/actions/globalActn';
import Reports from '../components/Reports';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const ReportsCnt = connect(mapStateToProps, mapDispatchToProps)(Reports);
export default ReportsCnt;
