import {
  EyeFilled, PieChartFilled, PlusCircleOutlined,
} from '@ant-design/icons';
import { Button, Table } from 'antd';
import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import styled from 'styled-components';

import { NormalText, NormalText3 } from '../../common/texts/Normal';
import { TitleText2 } from '../../common/texts/Titles';
import detail from '../../data/detail';
import { textColumn, twoDecimalColumn } from '../../helpers/columnDefinition';
import { createNotification } from '../../helpers/notifications';

class Cashbox extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
  })

  getNewSaleButtonConfigStyle = () => ({
    fontSize: '12pt',
    fontWeight: 600,
    marginLeft: 20,
    height: 50,
    width: '200px',
    borderRadius: 5,
    backgroundColor: '#2185D0',
  })

  getColumns = () => [
    textColumn('Correlativo', 'doc', '10%', 'left'),
    textColumn('Hora/Fecha', 'purchasedate', '10%', 'left'),
    twoDecimalColumn('Monto', 'total', '10%', 'left', '$'),
    {
      title: 'Vacas',
      dataIndex: 'amount',
      key: 'amount',
      width: '8%',
      ellipsis: true,
      align: 'center',
      render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${value}`}</p>,
    },
    {
      title: 'Producto',
      dataIndex: 'descrip',
      width: '44%',
      ellipsis: true,
      key: 'descrip',
      render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${value}`}</p>,
    },
    {
      title: 'Marca',
      dataIndex: 'brand',
      width: '10%',
      key: 'brand',
      render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${value}`}</p>,
    },
    {
      title: 'Medida',
      dataIndex: 'measurement',
      width: '10%',
      key: 'measurement',
      render: value => <p style={{ margin: 0, fontSize: '12pt' }}>{`${value}`}</p>,
    },
    {
      title: '',
      dataIndex: 'key',
      width: '10%',
      key: 'key',
      render: value => (
        <div>
          <Button
            style={{ backgroundColor: '#DEDEDE' }}
            type="secondary"
            size="small"
            onClick={
              () => {
                createNotification('info', `Selected id: ${value}`, 'Funcionalidad en desarrollo');
              }
            }
            icon={<EyeFilled style={{ color: '#000' }} />}
          />
        </div>
      ),
    },
  ]

  render() {
    return (
      <Wrapper>
        <div className="details-cnt">
          <div className="detail">
            <NormalText3 content={'Fecha actual: 12 de diciembre, 2020'} />
          </div>
          <div className="detail">
            <NormalText3 content={'Saldo inicial'} />
            <NormalText content={'$2,500.60'} />
          </div>
          <div className="detail">
            <NormalText3 content={'Salidas hoy'} />
            <NormalText content={'$2,500.60'} />
          </div>
          <div className="detail">
            <NormalText3 content={'Entradas hoy'} />
            <NormalText content={'$2,500.60'} />
          </div>
          <div className="detail">
            <NormalText3 content={'Saldo actual'} />
            <TitleText2 content={'$2,500.60'} bold color={'#2185D0'} />
          </div>
        </div>
        <div className="movements-cnt">
          <div className="header">
            <NormalText content={'Historial de Movimientos'} bold />
            <div className="dec-line" />
            <Button
              style={{
                width: 40, height: 40, backgroundColor: '#FFF', border: 'none',
              }}
              shape="circle"
              icon={(
                <PieChartFilled style={{
                  marginRight: 10, marginLeft: 10, fontSize: 24, color: '#2185D0',
                }}
                />
              )}
              onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
            />
            <Button
              type={'primary'}
              style={this.getNewSaleButtonConfigStyle()}
              icon={<PlusCircleOutlined style={{ marginRight: 15 }} />}
              onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
            >
              Registrar nuevo
            </Button>
          </div>
          <div className="table-container">
            <Table
              columns={this.getColumns()}
              dataSource={detail}
              style={{ width: '100%' }}
              pagination={{ pageSize: 9 }}
              scroll={{ scrollToFirstRowOnChange: true }}
              size="small"
            />
          </div>
        </div>
      </Wrapper>
    );
  }
}

// Cashbox.propTypes = {

// }

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  min-height: calc(100vh - 75px);
  .details-cnt, .movements-cnt {
    background-color: white;
    box-shadow: 3px 3px 10px 1px #DDDDDD;
    margin: 30px;
  }
  .details-cnt {
    display: flex;
    flex-direction: column;
    width: calc(25% - 60px);
    .detail {
      margin: 10px;
      width: calc(100% - 20px);
    }
  }
  .movements-cnt {
    width: calc(75% - 80px);
    height: calc(100vh - 120px);
    .header {
      margin: 10px 30px;
      width: calc(100% - 60px);
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-around;
      .dec-line {
        width: calc(100% - 575px);
        margin-left: 30px;
        margin-right: 30px;
        height: 2px;
        background-color: #2185D0;
      }
    }
    .table-container {
      margin: 10px 30px;
      width: calc(100% - 60px);
      // height: 100%;
      .ant-table table { font-size: 12pt; }
    }
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

export default Cashbox;
