import { CopyOutlined, SyncOutlined } from '@ant-design/icons';
import { Checkbox, Input, Select } from 'antd';
import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import styled from 'styled-components';

import { NormalText2 } from '../../common/texts/Normal';
import { createNotification } from '../../helpers/notifications';

const { Option } = Select;

class Settings extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    fetchinGeneralInfo: false,
  })

  onChange = (value) => {
    console.log(`selected ${value}`);
  }

  onSearch = (val) => {
    console.log('search:', val);
  }

  render() {
    return (
      <Wrapper>
        <div className="settings-cnt">
          <div className="section">
            <div className="item">
              <NormalText2 className="label" content="Nombre" />
              <div className="element">
                <Input placeholder="Gazu Gas" style={{ width: '50%' }} />
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Razn social" />
              <div className="element">
                <Input placeholder="Frangeli, S.A. de C.V." style={{ width: '50%' }} />
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Giro" />
              <div className="element">
                <Input placeholder="Ventas" style={{ width: '50%' }} />
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="NIT" />
              <div className="element">
                <Input placeholder="0000-000000-000-0" style={{ width: '50%' }} />
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="NRC" />
              <div className="element">
                <Input placeholder="000000-0" style={{ width: '50%' }} />
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Telfono" />
              <div className="element">
                <Input placeholder="7777-7777" style={{ width: '50%' }} />
              </div>
            </div>
          </div>
          <div className="section">
            <div className="item">
              <NormalText2 className="label" content="Impresora" />
              <div className="element">
                <Select
                  showSearch
                  placeholder="Impresora"
                  optionFilterProp="children"
                  onChange={this.onChange}
                  onSearch={this.onSearch}
                  filterOption={
                  (input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                  style={{ width: '50%' }}
                >
                  <Option value={1}>Epsom T-220</Option>
                  <Option value={2}>Epsom T-221</Option>
                  <Option value={3}>Epsom T-222</Option>
                </Select>
                <button
                  className="copy-label"
                  type="button"
                  onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
                >
                  <SyncOutlined style={{ marginLeft: 5, marginBottom: 5 }} />
                </button>
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="PIN" />
              <div className="element">
                <Input type="password" placeholder="****" style={{ width: '50%' }} />
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Confirmar envos automticos" />
              <div className="element">
                <Checkbox onChange={(value) => console.log(value)} />
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Correo para reportes" />
              <div className="element">
                <Input placeholder="email@example.com" style={{ width: '50%' }} />
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Bloquear despus de" />
              <div className="element">
                <Input type="number" placeholder="1234" style={{ width: 70, marginRight: 15, textAlign: 'center' }} />
                <NormalText2 content="Minutos de inactividad" />
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Zona Horaria" />
              <div className="element">
                <Select
                  showSearch
                  placeholder="Zona Horaria"
                  optionFilterProp="children"
                  onChange={this.onChange}
                  onSearch={this.onSearch}
                  filterOption={
                  (input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                  style={{ width: '50%' }}
                >
                  <Option value={1}>Epsom T-220</Option>
                  <Option value={2}>Epsom T-221</Option>
                  <Option value={3}>Epsom T-222</Option>
                </Select>
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Idioma" />
              <div className="element">
                <Select
                  showSearch
                  placeholder="Idioma"
                  optionFilterProp="children"
                  onChange={this.onChange}
                  onSearch={this.onSearch}
                  filterOption={
                  (input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                  style={{ width: '50%' }}
                >
                  <Option value={1}>Espaol</Option>
                </Select>
              </div>
            </div>
          </div>
          <div className="section">
            <div className="item">
              <NormalText2 className="label" content="ID Licencia" />
              <div className="element">
                <p>SMSG001003</p>
                <button
                  className="copy-label"
                  type="button"
                  onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
                >
                  <CopyOutlined style={{ marginLeft: 5, marginBottom: 5 }} />
                </button>
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Suscripcin" />
              <div className="element">
                <p>Mensual</p>
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Vence" />
              <div className="element">
                <p>Lunes, 28/01/2000</p>
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="Propietario" />
              <div className="element">
                <p>Julio Carranza</p>
              </div>
            </div>
            <div className="item">
              <NormalText2 className="label" content="ltimo pago" />
              <div className="element">
                <p>Lunes, 01/01/1956</p>
              </div>
            </div>
          </div>
        </div>
      </Wrapper>
    );
  }
}

// Settings.propTypes = {

// }

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: calc(100vh - 75px);
  .settings-cnt {
    margin: 30px;
    background-color: white;
    width: calc(100% - 120px);
    height: calc(100vh - 120px);
    box-shadow: 3px 3px 10px 1px #DDDDDD;
    overflow-y: scroll;
    ::-webkit-scrollbar {
      width: 8px;
      height: 3px;
    }
    ::-webkit-scrollbar-track {
      border-radius: 3px;
    }
    ::-webkit-scrollbar-thumb {
      background: #D5D5D5;
      width: 8px;
      height: 3px;
      border-radius: 3px;
    }
    .section {
      margin: 20px;
      width: calc(100% - 40px);
      border: 1px solid #F2F2F2;
      .item {
        margin: 20px 25px;
        width: calc(100% - 50px);
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        p { margin: 0px; }
        .label, .element {
          width: 50%;
          display: flex;
          flex-direction: row;
          align-items: center;
          .ant-input {
            border: none;
            border-radius: 0px;
            border-bottom: 1px solid #d9d9d9;
          }
          .ant-input:focus {
            border: none;
            border-bottom: 1px solid #2185D0 !important;
            box-shadow: none;
          }
          .ant-select-selector {
            border: none;
            border-radius: 0px;
            border-bottom: 1px solid #d9d9d9;
          }
          .ant-select-focused .ant-select-selector,
          .ant-select-selector:focus,
          .ant-select-selector:active,
          .ant-select-open .ant-select-selector {
            border-color: #d9d9d9 !important;
            border-bottom: 1px solid #2185D0 !important;
            box-shadow: none !important;
          }
          .copy-label {
            padding: 0px;
            margin-left: 15px;
            border: none;
            outline: none;
            background-color: transparent;
            font-size: 13px;
            :hover {
              color: #2185D0;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

export default Settings;
