import { connect } from 'react-redux';

import { getMethod } from '../../../store/actions/globalActn';
import Purchases from '../../components/InventorySections/Purchases';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const PurchasesCnt = connect(mapStateToProps, mapDispatchToProps)(Purchases);
export default PurchasesCnt;
