import { connect } from 'react-redux';

import { getMethod } from '../../../store/actions/globalActn';
import Measurements from '../../components/InventorySections/Measurements';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const MeasurementsCnt = connect(mapStateToProps, mapDispatchToProps)(Measurements);
export default MeasurementsCnt;
