import { connect } from 'react-redux';

import { getMethod } from '../../store/actions/globalActn';
import Settings from '../components/Settings';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const SettingsCnt = connect(mapStateToProps, mapDispatchToProps)(Settings);
export default SettingsCnt;
