import { connect } from 'react-redux';

import { getMethod } from '../../store/actions/globalActn';
import Cashbox from '../components/Cashbox';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const CashboxCnt = connect(mapStateToProps, mapDispatchToProps)(Cashbox);
export default CashboxCnt;
