import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Route, Switch } from 'react-router-dom';
import {
  MenuOutlined, ShoppingCartOutlined, SettingOutlined, SearchOutlined,
  InboxOutlined, UserOutlined, DollarCircleOutlined, LineChartOutlined,
  LockOutlined,
} from '@ant-design/icons';
import {
  Drawer, Button, Menu, Input,
} from 'antd';

import SalesCnt from '../container/SalesCnt';
import InventoryCnt from '../container/InventoryCnt';
import CustomersCnt from '../container/CustomersCnt';
import CashboxCnt from '../container/CashboxCnt';
import ReportsCnt from '../container/ReportsCnt';
import SettingsCnt from '../container/SettingsCnt';
import { createNotification } from '../../helpers/notifications';

const prefix = (
  <SearchOutlined style={{ fontSize: 18, color: '#2185D0' }} />
);

class Dashboard extends Component {
  isFullyMounted = false;

  constructor(props) {
    super(props);
    this.state = this.getInitialState()
  }

  componentDidMount() {
    this.defineMenuSelectedPath(this.props.location.pathname)
    this.isMounted = true;
  }

  componentWillUnmount() {
    this.isMounted = false;
  }

  set isMounted(status) { this.isFullyMounted = status }

  get isMounted() { return this.isFullyMounted }

  getInitialState = () => ({
    openMenuDrawer: false,
    drawerIndex: '1',
    moduleTitle: 'Ventas',
  })

  goToNClose = (path, index, title) => {
    const { goTo } = this.props;
    this.setState({ openMenuDrawer: false, drawerIndex: index, moduleTitle: title });
    goTo(path);
  }

  defineMenuSelectedPath = (path) => {
    switch (true) {
      case path === '/': this.setState({ drawerIndex: '1', moduleTitle: 'Ventas' }); break;
      case path.includes('/inventory'): this.setState({ drawerIndex: '2', moduleTitle: 'Inventario' }); break;
      case path.includes('/customers'): this.setState({ drawerIndex: '3', moduleTitle: 'Clientes' }); break;
      case path.includes('/cashbox'): this.setState({ drawerIndex: '4', moduleTitle: 'Caja' }); break;
      case path.includes('/reports'): this.setState({ drawerIndex: '5', moduleTitle: 'Reportes' }); break;
      case path.includes('/settings'): this.setState({ drawerIndex: '6', moduleTitle: 'Configuracin' }); break;
      default: this.setState({ drawerIndex: '1', moduleTitle: 'Ventas' }); break;
    }
  }

  render() {
    const { openMenuDrawer, drawerIndex, moduleTitle } = this.state;
    return (
      <Wrapper>
        <NavbarBody>
          <div className="drawer-button-cnt">
            <Button
              style={{
                width: 40, height: 40, backgroundColor: '#2185D0', border: 'none',
              }}
              shape="circle"
              icon={<MenuOutlined style={{ fontSize: 25, color: '#FFF' }} />}
              onClick={() => this.setState({ openMenuDrawer: true })}
            />
          </div>
          <div className="title-cnt">
            <p>{moduleTitle}</p>
          </div>
          <div className="search-box-cnt">
            <Input
              placeholder="Buscar"
              prefix={prefix}
              style={{
                width: 320, padding: '5px 10px', marginLeft: '25px', borderRadius: '5px',
              }}
            />
          </div>
          <div className="lock-button-cnt">
            <Button
              style={{ backgroundColor: '#FFF', border: 'none', margin: '0px 15px' }}
              icon={<ShoppingCartOutlined style={{ color: '#2185D0' }} />}
              onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
            >
              Nueva venta
            </Button>
            <Button
              style={{
                width: 40, height: 40, backgroundColor: '#2185D0', border: 'none', margin: '0px 15px',
              }}
              shape="circle"
              icon={<LockOutlined style={{ fontSize: 25, color: '#FFF' }} />}
              onClick={() => createNotification('info', 'En desarrollo', 'Funcionalidad en desarrollo')}
            />
          </div>
        </NavbarBody>
        <RoutesContainer>
          <Switch>
            <Route
              exact
              path="/"
              component={SalesCnt}
            />
            <Route
              // exact
              path="/inventory"
              component={InventoryCnt}
            />
            <Route
              exact
              path="/customers"
              component={CustomersCnt}
            />
            <Route
              exact
              path="/cashbox"
              component={CashboxCnt}
            />
            <Route
              exact
              path="/reports"
              component={ReportsCnt}
            />
            <Route
              exact
              path="/settings"
              component={SettingsCnt}
            />
          </Switch>
        </RoutesContainer>
        <Drawer
          title="Gazo Gas"
          placement="left"
          closable
          onClose={() => this.setState({ openMenuDrawer: false })}
          bodyStyle={{ padding: 0 }}
          visible={openMenuDrawer}
        >
          <Menu
            style={{ width: '100%' }}
            selectedKeys={[drawerIndex]}
            mode="inline"
          >
            <Menu.Item key="1" icon={<ShoppingCartOutlined />} onClick={() => this.goToNClose('/', '1', 'Ventas')}>
              Ventas
            </Menu.Item>
            <Menu.Item key="2" icon={<InboxOutlined />} onClick={() => this.goToNClose('/inventory', '2', 'Inventario')}>
              Inventarios
            </Menu.Item>
            <Menu.Item key="3" icon={<UserOutlined />} onClick={() => this.goToNClose('/customers', '3', 'Clientes')}>
              Clientes
            </Menu.Item>
            <Menu.Item key="4" icon={<DollarCircleOutlined />} onClick={() => this.goToNClose('/cashbox', '4', 'Caja')}>
              Caja
            </Menu.Item>
            <Menu.Item key="5" icon={<LineChartOutlined />} onClick={() => this.goToNClose('/reports', '5', 'Reportes')}>
              Reportes
            </Menu.Item>
            <Menu.Item key="6" icon={<SettingOutlined />} onClick={() => this.goToNClose('/settings', '6', 'Configuracin')}>
              Configuracin
            </Menu.Item>
          </Menu>
        </Drawer>
      </Wrapper>
    );
  }
}

Dashboard.propTypes = {
  goTo: PropTypes.func.isRequired,
  location: PropTypes.instanceOf(Object).isRequired,
}

const Wrapper = styled.div`
  min-height: 100vh;
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {

  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {

  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {

  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {

  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {

  }
`

const NavbarBody = styled.div`
  display: flex;
  flex-direction: row;
  background-color: #2185D0;
  color: #FFF;
  height: 60px;
  .drawer-button-cnt, .title-cnt, .search-box-cnt,
  .lock-button-cnt {
    margin: 0px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .title-cnt {
    p {
      margin: 0px;
      font-size: 24px;
    }
  }
  .lock-button-cnt {
    flex-direction: row;
    margin-left: auto;
    margin-right: 0;
  }
  /* DESKTOPS | 1281px to higher resolution */
  @media (min-width: 1281px) {
    
  }
  /* DESKTOPS - LAPTOPS | 1025px to 1281px */
  @media (min-width: 1025px) and (max-width: 1280px) {
    
  }
  /* TABLETS - IPADS (Portrait) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) {
    
  }
  /* TABLETS - IPADS (Landscape) | 768px to 1024px */
  @media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
    
  }
  /* LOW RESOLUTION TABLES - MOBILES (Landscape) | 481px to 767px */
  @media (min-width: 481px) and (max-width: 767px) {
    
  }
  /* MOST OF THE SMARTPHONES MOBILES (PORTRAIT) */
  @media (min-width: 320px) and (max-width: 480px) {
    
  }
`;

const RoutesContainer = styled.div`
  background-color: #F2F2F2;
  min-height: calc(100vh - 60px);
`

export default Dashboard;
