import { connect } from 'react-redux';

import { getMethod } from '../../../store/actions/globalActn';
import Stocks from '../../components/InventorySections/Stocks';

const mapStateToProps = (state) => ({
  servermessage: state.globalRdcr.servermessage,
})

const mapDispatchToProps = (dispatch) => ({
  getMethod: (path) => dispatch(getMethod(path)),
})

const StocksCnt = connect(mapStateToProps, mapDispatchToProps)(Stocks);
export default StocksCnt;
